@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    margin-top: 15rem;
    background-color: lighten(v.$azulGrisFuerte, 60%);
    padding: v.$separacion 0;

    &__logo {
        width: 7rem;
        margin: 0 auto v.$separacion auto;

        @include m.tablet { 
            margin: 0 2rem 0 0;
        }
    }

    &__contenido {
        @include m.tablet { 
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }
}

.attribution {
    margin-top: 5rem;
    color: v.$azulGris;  
    a {
        color: v.$azulGrisFuerte;
        font-weight: 700;
        transition-property: color;
        transition-duration: .1s;

        &:hover {
            color: v.$primario;
        }
    }
}