@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    background-repeat: no-repeat;
    background-size: cover;

    &__contenido {
        padding-top: calc(v.$separacion * 2);
    }
    &__logo {
        margin: 0 auto;
    }
    &__info {
        margin:  v.$separacion 0;
        text-align: center;
    }
    &__heading {
        
    }
    &__texto {
        @include m.tablet { 
            padding: 0 10rem;
        }
        @include m.desktop { 
            padding: 0 30rem;
        }
    }
}

.notavif.notwebp .header {
    background-image: url(../img/bg-header-mobile.png);
}
.webp .header {
    background-image: url(../img/bg-header-mobile.webp);
}
.avif .header {
    background-image: url(../img/bg-header-mobile.avif);
}

@include m.tablet { 
    .notavif.notwebp .header {
        background-image: url(../img/bg-header-desktop.png);
    }
    .webp .header {
        background-image: url(../img/bg-header-desktop.webp);
    }
    .avif .header {
        background-image: url(../img/bg-header-desktop.avif);
    }
}