@use 'base/variables' as v;
@use 'base/mixins' as m;

.enlaces {
    display: flex;
    flex-direction: column;
    gap: 2rem;

    @include m.tablet { 
        flex-direction: row;
        justify-content: center;
    }
}

.enlace {
    
    &--verde {
        @include m.boton(v.$primario, v.$blanco);
        border-bottom: .5rem solid darken(v.$primario, 10%);
        box-shadow: 0px 5px 15px -5px lighten(v.$primario, 10%);
        transition-property: opacity;
        transition-duration: .2s;

        &:hover {
            opacity: .8;
        }
    }

    &--azul {
        @include m.boton(v.$secundario, v.$blanco);
        border-bottom: .5rem solid darken(v.$secundario, 9%);
        box-shadow: 0px 5px 15px -5px darken(v.$secundario, 90%);
        transition-property: opacity;
        transition-duration: .2s;

        &:hover {
            opacity: .8;
        }
    }
}