@use 'base/variables' as v;
@use 'base/mixins' as m;

.redes {
    display: flex;
    justify-content: space-evenly;
    padding: 0 6rem;
    margin-top: 2rem;
    
    &__imagen {
        cursor: pointer;
        transition-property: filter;
        transition-duration: .1s;

        &:hover {
            filter: invert(58%) sepia(91%) saturate(338%) hue-rotate(122deg) brightness(88%) contrast(94%);
        }
    }

    @include m.tablet { 
        gap: 3rem;
    }
}