@use 'base/variables' as v;
@use 'base/mixins' as m;

.logos {

    &__grid {
        @include m.tablet { 
            @include m.grid(5, 2rem);
            align-items: center;
        }
    }

    &__imagen {
        margin: 0 auto;
        margin-bottom: v.$separacion;
        &:last-of-type {
            margin-bottom: 0;
        }
        @include m.tablet { 
            margin-bottom: 0;
        }
    }
}