@use 'variables' as v; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}

p {
    color: v.$negro;
    font-size: 2rem;
}
.contenedor {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
}
img {
    max-width: 100%;
    display: block;
}

.seccion {
    padding: 15rem 0 0 0;
}

[class$="__heading"] {
    color: lighten(v.$azulGris, 20%);
}

[class*="__texto"] {
    color: lighten(v.$azulGrisFuerte, 20%);
}