/** Media Queries **/
.text-center {
  text-align: center;
}

.mt-5 {
  margin-top: 5rem;
}

.centrado {
  margin: 0 auto;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Bai Jamjuree", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

.seccion {
  padding: 15rem 0 0 0;
}

[class$=__heading] {
  color: #3b444a;
}

[class*=__texto] {
  color: #7b8793;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

.header {
  background-repeat: no-repeat;
  background-size: cover;
}
.header__contenido {
  padding-top: 10rem;
}
.header__logo {
  margin: 0 auto;
}
.header__info {
  margin: 5rem 0;
  text-align: center;
}
@media (min-width: 768px) {
  .header__texto {
    padding: 0 10rem;
  }
}
@media (min-width: 1024px) {
  .header__texto {
    padding: 0 30rem;
  }
}

.notavif.notwebp .header {
  background-image: url(../img/bg-header-mobile.png);
}

.webp .header {
  background-image: url(../img/bg-header-mobile.webp);
}

.avif .header {
  background-image: url(../img/bg-header-mobile.avif);
}

@media (min-width: 768px) {
  .notavif.notwebp .header {
    background-image: url(../img/bg-header-desktop.png);
  }
  .webp .header {
    background-image: url(../img/bg-header-desktop.webp);
  }
  .avif .header {
    background-image: url(../img/bg-header-desktop.avif);
  }
}
.enlaces {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .enlaces {
    flex-direction: row;
    justify-content: center;
  }
}

.enlace--verde {
  text-align: center;
  background-color: #26baa4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 1rem;
  border-radius: 4rem;
  font-size: 2rem;
  font-weight: 700;
  border-bottom: 0.5rem solid #1d907f;
  box-shadow: 0px 5px 15px -5px #3cd7c0;
  transition-property: opacity;
  transition-duration: 0.2s;
}
.enlace--verde:hover {
  opacity: 0.8;
}
.enlace--azul {
  text-align: center;
  background-color: hsl(233, 100%, 69%);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 1rem;
  border-radius: 4rem;
  font-size: 2rem;
  font-weight: 700;
  border-bottom: 0.5rem solid #334bff;
  box-shadow: 0px 5px 15px -5px black;
  transition-property: opacity;
  transition-duration: 0.2s;
}
.enlace--azul:hover {
  opacity: 0.8;
}

.snippets__info {
  text-align: center;
  margin-bottom: 5rem;
}

.grid {
  text-align: center;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .grid {
    width: 100%;
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    text-align: left;
  }
}
@media (min-width: 768px) {
  .grid__imagen {
    margin-left: -5rem;
  }
}
@media (min-width: 1024px) {
  .grid__imagen-monitor {
    width: 90%;
  }
}
.grid__info {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .grid__info {
    margin-top: 0;
  }
}

.punto {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .punto {
    padding-right: 4rem;
  }
}
@media (min-width: 1024px) {
  .punto {
    padding-right: 10rem;
  }
}
@media (min-width: 1200px) {
  .punto {
    padding-right: 30rem;
  }
}
@media (min-width: 1800px) {
  .punto {
    padding-right: 40rem;
  }
}
.punto:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .punto__heading {
    font-size: 2.2rem;
  }
}
@media (min-width: 768px) {
  .punto__texto {
    font-size: 1.8rem;
  }
}

.workflow__grid {
  margin-top: 8rem;
}
@media (min-width: 768px) {
  .workflow__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.info {
  margin-bottom: 6rem;
}
.info:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .info {
    margin-bottom: 0;
  }
}
.info__imagen {
  margin-bottom: 2rem;
  height: 4rem;
  display: flex;
  align-items: center;
}
.info__texto {
  margin: 2rem 0 0 0;
}

@media (min-width: 768px) {
  .logos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.logos__imagen {
  margin: 0 auto;
  margin-bottom: 5rem;
}
.logos__imagen:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .logos__imagen {
    margin-bottom: 0;
  }
}

.footer {
  margin-top: 15rem;
  background-color: #ebedef;
  padding: 5rem 0;
}
.footer__logo {
  width: 7rem;
  margin: 0 auto 5rem auto;
}
@media (min-width: 768px) {
  .footer__logo {
    margin: 0 2rem 0 0;
  }
}
@media (min-width: 768px) {
  .footer__contenido {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.attribution {
  margin-top: 5rem;
  color: hsl(201, 11%, 6%);
}
.attribution a {
  color: hsl(210, 10%, 33%);
  font-weight: 700;
  transition-property: color;
  transition-duration: 0.1s;
}
.attribution a:hover {
  color: #26baa4;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    align-items: start;
  }
}
.navegacion__links {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion__links {
    align-items: start;
    gap: 2rem;
    margin-right: 4rem;
  }
}
.navegacion__link {
  font-size: 1.8rem;
  margin-bottom: 3rem;
  color: hsl(210, 10%, 33%);
  transition-property: color;
  transition-duration: 0.1s;
}
.navegacion__link:hover {
  color: #26baa4;
}
@media (min-width: 768px) {
  .navegacion__link {
    margin-bottom: 0;
  }
}

.redes {
  display: flex;
  justify-content: space-evenly;
  padding: 0 6rem;
  margin-top: 2rem;
}
.redes__imagen {
  cursor: pointer;
  transition-property: filter;
  transition-duration: 0.1s;
}
.redes__imagen:hover {
  filter: invert(58%) sepia(91%) saturate(338%) hue-rotate(122deg) brightness(88%) contrast(94%);
}
@media (min-width: 768px) {
  .redes {
    gap: 3rem;
  }
}