@use 'variables' as v; 

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktop2 {
    @media (min-width: v.$desktop2) {
        @content;
    }
}

@mixin desktop3 {
    @media (min-width: v.$desktop3) {
        @content;
    }
}

@mixin boton($bgColor, $colorTexto) {
    text-align: center;
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    margin-top: 1rem;
    border-radius: 4rem;
    font-size: 2rem;
    font-weight: 700;
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}