@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet { 
        flex-direction: row;
        align-items: start;
    }

    &__links {
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet { 
            align-items: start;
            gap: 2rem;
            margin-right: 4rem;
        }
    }

    &__link {
        font-size: 1.8rem;
        margin-bottom: 3rem;
        color: v.$azulGrisFuerte;
        transition-property: color;
        transition-duration: .1s;

        &:hover {
            color: v.$primario;
        }

        @include m.tablet { 
            margin-bottom: 0;
        }
    }
}