@use 'base/variables' as v;
@use 'base/mixins' as m;

.workflow {

    &__grid {
        margin-top: 8rem;

        @include m.tablet { 
            @include m.grid(3, 2rem);
        }
    }
}

.info {
    margin-bottom: 6rem;
    &:last-of-type {
        margin-bottom: 0;
    }
    @include m.tablet { 
        margin-bottom: 0;
    }

    &__imagen {
        margin-bottom: 2rem;
        height: 4rem;
        display: flex;
        align-items: center;
    }

    &__texto {
        margin: 2rem 0 0 0;
    }
}