@use 'base/variables' as v;
@use 'base/mixins' as m;

.grid {
    text-align: center;
    width: 90%;
    margin: 0 auto;

    @include m.tablet { 
        width: 100%;
        @include m.grid(2, 4rem);
        align-items: center;
        text-align: left;
    }

    &__imagen {
        @include m.tablet { 
            margin-left: -5rem;
        }

        &-monitor {
            @include m.desktop { 
                width: 90%;
            }
        }
    }

    &__info {
        margin-top: v.$separacion;
        @include m.tablet { 
            margin-top: 0;
        }
    }
}

.punto {
    margin-bottom: v.$separacion;

    @include m.tablet { 
        padding-right: 4rem;
    }
    @include m.desktop { 
        padding-right: 10rem;
    }
    @include m.desktop2 {
        padding-right: 30rem;
    }
    @include m.desktop3 { 
        padding-right: 40rem;
    }

    &:last-of-type {
        margin-bottom: 0;
    }

    &__heading {
        @include m.tablet { 
            font-size: 2.2rem;
        }
    }

    &__texto {
        @include m.tablet { 
            font-size: 1.8rem;
        }
    }
}